import React, { Component } from "react";
import Stepper from "react-stepper-horizontal";
import axios from "axios";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import validationSupplier from "../../Validations/validationSupplier";

const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input {...input} type={type} className="form-control" maxLength={limit} />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      step: 0,
      listKota: [],
      status: "AKTIF",
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
    };
  }

  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    } else if (step === 1) {
      this.setState({ step: step + 1, step2: "row d-none", step3: "row" });
    }
  };

  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    } else if (step === 2) {
      this.setState({ step: step - 1, step3: "row d-none", step2: "row" });
    }
  };

  componentDidMount() {
    axios
      .get(server + "citys")
      .then((res) =>
        this.setState({
          listKota: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="col-md-12">
            <Stepper
              steps={[
                { title: "Data Diri" },
                { title: "Contact Person" },
                // { title: "Data Bank" },
              ]}
              activeStep={this.state.step}
            />
          </div>
          <div className={this.state.step1} id="step-0">
            <div className="col-md-4 col-lg-4">
              <Field
                name="kodeSupplier"
                type="text"
                component={renderField}
                label="Kode Supplier"
                limit="5"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-2 col-lg-2">
              <Field
                name="kodeExt"
                type="text"
                component={renderField}
                label="Kode Ext"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-6 col-lg-6">
              <Field
                name="namaSupplier"
                type="text"
                component={renderField}
                label="Nama Supplier"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <label htmlFor="" className="text-black">
                Kota
              </label>
              <Field
                name="kodeKota"
                type="text"
                component="select"
                className="form-control"
              >
                <option value="DEFUALT | DEFUALT">SILAHKAN PILIH</option>
                {this.state.listKota.map((list) => (
                  <option
                    value={list.kodeKota + "|" + list.namaKota}
                    key={list.kodeKota}
                  >
                    {list.namaKota}
                  </option>
                ))}
              </Field>
            </div>
            <div className="col-md-6 col-lg-6">
              <Field
                name="Alamat"
                type="text"
                component={renderField}
                label="Alamat"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-2 col-lg-2">
              <Field
                name="kodePos"
                type="number"
                component={renderField}
                label="Kode Pos"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={() => this.nextStep()}
                >
                  Next
                </button>
              </div>
            </div>
          </div>
          <div className={this.state.step2} id="step-1">
            <div className="col-md-4 col-lg-4">
              <Field
                name="contactPerson"
                type="text"
                component={renderField}
                label="Contact Person"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="Email"
                type="email"
                component={renderField}
                label="Email"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="telepon"
                type="tel"
                component={renderField}
                label="Telepon"
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="fax"
                type="tel"
                component={renderField}
                label="Fax"
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="handPhone"
                type="tel"
                component={renderField}
                label="Handphone"
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="row">
                <div className="text-left col-md-6">
                  <button
                    name="simpan"
                    id="simpan"
                    type="button"
                    className="btn btn-primary"
                    onClick={() => this.prevStep()}
                  >
                    Back
                  </button>
                </div>
                <div className="text-right col-md-6">
                  <button
                    name="simpan"
                    id="simpan"
                    type="submit"
                    className="btn btn-primary"
                    onClick={() =>
                      this.setState({
                        step1: "row",
                        step2: "row d-none",
                        step3: "row d-none",
                        step: 0,
                      })
                    }
                  >
                    Simpan
                  </button>
                </div>
              </div>
            </div>
          </div>
          {/* <div className={this.state.step3} id="step-1">
            <div className="col-md-4 col-lg-4">
              <Field
                name="namaBank"
                type="text"
                component={renderField}
                label="Nama Bank"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="nomorRekening"
                type="number"
                component={renderField}
                label="Nomor Rekneing"
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="atasNama"
                type="text"
                component={renderField}
                label="Atas Nama"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="row">
                <div className="text-left col-md-6">
                  <button
                    name="simpan"
                    id="simpan"
                    type="button"
                    className="btn btn-primary"
                    onClick={() => this.prevStep()}
                  >
                    Back
                  </button>
                </div>
                <div className="text-right col-md-6">
                  <button
                    type="submit"
                    className="btn btn-primary"
                    disabled={this.props.submitting}
                  >
                    Simpan
                  </button>
                </div>
              </div>
            </div>
          </div> */}
        </form>
      </div>
    );
  }
}
FormSupplier = reduxForm({
  form: "SupplierTambah",
  validate: validationSupplier,
  enableReinitialize: true,
})(FormSupplier);
export default connect()(FormSupplier);
