import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import axios from "axios";
import FormSupplier from "./Component/FormSupplier";
import { connect } from "react-redux";
import { editsupplier, getsupplierList } from "../actions/supplierAction";
import notrespond from "../Images/500.svg";
import Swal from "sweetalert2";
import FormSupplierEdit from "./Component/FormSupplierEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      axios
        .delete(server + "supplier", {
          data: {
            kodeSupplier: kode,
          },
        })
        .then((res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .catch((err) =>
          Swal.fire({
            position: "top-end",
            icon: "error",
            text: "Data Gagal Dihapus!",
            showConfirmButton: false,
            timer: 1500,
          })
        )
        .then(() => dispatch(getsupplierList()));
    }
  });
}

const expandRow = {
  renderer: (row) => (
    <div>
      <table>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Kode Ext</td>
          <td>:</td>
          <td>{row.kodeExt}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Email</td>
          <td>:</td>
          <td>{row.info.email}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Alamat</td>
          <td>:</td>
          <td>{row.info.alamat}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Kode Pos</td>
          <td>:</td>
          <td>{row.info.kodePos}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Contact Person</td>
          <td>:</td>
          <td>{row.info.contactPerson}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Telephone</td>
          <td>:</td>
          <td>{row.info.telephone}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Fax</td>
          <td>:</td>
          <td>{row.info.fax}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Handphone</td>
          <td>:</td>
          <td>{row.info.handPhone}</td>
        </tr>
      </table>
    </div>
  ),
};
const maptoState = (state) => {
  return {
    supplierList: state.provinsi.supplier,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

const server = process.env.REACT_APP_BACKEND_URL;

class Supplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kodeSupplier",
          text: "Kode Supplier",
        },
        {
          dataField: "namaSupplier",
          text: "Nama Supplier",
        },
        {
          dataField: "kota.kodeKota",
          text: "Kode Kota",
        },
        {
          dataField: "kota.namaKota",
          text: "Nama Kota",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kodeSupplier)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  data-target="#editModal"
                  data-toggle="modal"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editsupplier(
                        row.info.alamat,
                        row.info.email,
                        row.info.atasNama,
                        row.info.contactPerson,
                        row.info.fax,
                        row.info.handPhone,
                        row.kodeExt,
                        row.kota.kodeKota,
                        row.info.kodePos,
                        row.kodeSupplier,
                        row.info.namaBank,
                        row.namaSupplier,
                        row.info.nomorRekening,
                        row.info.telephone,
                        row.kota.namaKota
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
  }

  handleSubmit(data) {
    var kota = data.kodeKota ? data.kodeKota : "DEFALT | DEFAULT";
    var result_explode = kota.split("|");
    this.setState({
      kodeKota: result_explode[0],
      namaKota: result_explode[1],
    });
    console.log(data);
    console.log(result_explode[0] + result_explode[1]);
    const isi = {
      kota: {
        kodeKota: result_explode[0],
        namaKota: result_explode[1],
      },
      info: {
        email: data.Email,
        alamat: data.Alamat,
        kodePos: data.kodePos,
        contactPerson: data.contactPerson,
        telephone: data.telepon,
        fax: data.fax,
        handPhone: data.handPhone,
      },
      kodeSupplier: data.kodeSupplier,
      statusAktif: data.status ? data.status : "AKTIF",
      namaSupplier: data.namaSupplier,
      kodeExt: data.kodeExt,
    };
    console.log(isi);
    axios
      .post(server + "supplier", isi)
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Disimpan!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Disimpan!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getsupplierList()))
      .then(() => this.props.dispatch(reset("SupplierTambah")));
  }
  handleEdit(data) {
    var kota = data.kodeKota ? data.kodeKota : "DEFALT | DEFAULT";
    var result_explode = kota.split("|");
    this.setState({
      kodeKota: result_explode[0],
      namaKota: result_explode[1],
    });
    console.log(data);
    console.log(result_explode[0] + result_explode[1]);
    const isi = {
      kota: {
        kodeKota: result_explode[0],
        namaKota: result_explode[1],
      },
      info: {
        email: data.Email,
        alamat: data.Alamat,
        kodePos: data.kodePos,
        contactPerson: data.contactPerson,
        telephone: data.telepon,
        fax: data.fax,
        handPhone: data.handPhone,
      },
      kodeSupplier: data.kodeSupplier,
      statusAktif: data.status ? data.status : "AKTIF",
      namaSupplier: data.namaSupplier,
      kodeExt: data.kodeExt,
    };
    console.log(isi);
    axios
      .put(server + "supplier", isi)
      .then(
        (res) =>
          Swal.fire({
            position: "top-end",
            icon: "success",
            text: "Data Berhasil Dirubah!",
            showConfirmButton: false,
            timer: 1500,
          }),
        document.getElementById("dismiss_modal1").click()
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Dirubah!",
          showConfirmButton: false,
          timer: 1500,
        })
      )
      .then(() => this.props.dispatch(getsupplierList()));
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Data Supplier
                </h4>
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                    >
                      Tambah Data Supplier
                    </button>
                  </div>
                </div>
                {this.props.supplierList ? (
                  <ToolkitProvider
                    keyField="kodeSupplier"
                    data={this.props.supplierList}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="text-left mt-5 mb-3">
                        <div className="text-right">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Pencarian"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          expandRow={expandRow}
                          pagination={paginationFactory()}
                        />
                        <ExportCSVButton
                          {...props.csvProps}
                          className="btn btn-primary"
                        >
                          Export CSV
                        </ExportCSVButton>
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img
                          src={notrespond}
                          style={{ width: "30%" }}
                          alt="Not Respond
                          "
                        />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <Skeleton width={1100} height={180} />
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Supplier
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  name="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormSupplier onSubmit={(data) => this.handleSubmit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Edit Data Supplier
                </h4>
                <button
                  type="button"
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormSupplierEdit onSubmit={(data) => this.handleEdit(data)} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Supplier);
